.landing-navbar {
    height: 8vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 5px;
    background-color: var(--secondary) ;
 
    color: var(--secondary) !important;
}

.landing-nav-right {
    display: flex;
    flex-direction: row;
    font-size: 13px;
    font-family: var(--font2);
}

.landing-nav-right > * {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 8vh;
    margin-right: 20px;
}

